import { FC, useEffect } from "react";
import { Breadcrumb, PageHeader, Tabs } from "antd";
import { NavLink, useParams } from "react-router-dom";

import { RequisitiesBlock } from "./blocks/requisites";
import { useStore } from "entities/store";
import { MembersBlock } from "./blocks/members";
import { MeetingsBlock } from "./blocks/meetings";

const { TabPane } = Tabs;

const CompaniesDetailsPage: FC = () => {
  const { id } = useParams();

  const { company, getCompany } = useStore((state) => state);

  useEffect(() => {
    if (id) {
      getCompany(id);
    }
  }, [id]);

  if (!company) {
    return null;
  }

  return (
    <div className="wrapper">
      <Breadcrumb>
        <Breadcrumb.Item>
          <NavLink to="/companies">Главная</NavLink>
          {/* <a href="/">Главная</a> */}
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <NavLink to="/companies">Компании</NavLink>
          {/* <a href="/companies">Компании</a> */}
        </Breadcrumb.Item>
      </Breadcrumb>
      <div className="header">
        <h1>{"ООО " + company.requisites.name}</h1>
      </div>
      <Tabs
        defaultActiveKey="1"
        //   onChange={onChange}
      >
        <TabPane tab="Реквизиты" key="1">
          <RequisitiesBlock requisites={company.requisites} />
        </TabPane>
        <TabPane tab="Лица" key="2">
          <MembersBlock members={company.members} company_id={company.id} />
        </TabPane>
        <TabPane tab="Собрания" key="3">
          <MeetingsBlock meetings={company.meetings} company_id={company.id} />
        </TabPane>
      </Tabs>
    </div>
  );
};

export default CompaniesDetailsPage;
