import { useModal } from "@ebay/nice-modal-react";
import { Alert, Button, Card, Tag } from "antd";
import MemberForm from "entities/member/MemberForm";
import { IMember, MemberFormEnum, MEMBERS_TYPES } from "entities/member/types";
import { useStore } from "entities/store";
import { FC, useCallback } from "react";

interface IProps {
  members: IMember[];
  company_id: string;
}

export const MembersBlock: FC<IProps> = ({ members, company_id }) => {
  console.log("🚀 ~ file: index.tsx ~ line 14 ~ members", members)
  const userModal = useModal(MemberForm);
  const { addMember, changeMember } = useStore((state) => state);

  const handleNewItem = useCallback(() => {
    userModal.show().then((newMember: any) => {
      addMember({ ...newMember, company_id });
    });
  }, [userModal, members]);

  const handleChangeItem = useCallback(
    (member: any) => {
      userModal.show({ member }).then((newMember: any) => {
        changeMember({...newMember, id: member.id});
      });
    },
    [userModal, members]
  );

  const commonPart = members.reduce((acc, cur) => acc + +cur.part!, 0);

  // if (commonPart !== 100) {
  //   return (
  //     <Alert
  //       message="Общая доля участников не равна 100%. Проверьте доли участников компании."
  //       type="warning"
  //       // closable
  //       // onClose={onClose}
  //     />
  //   );
  // }

  return (
    <div>
      <Button type="primary" onClick={handleNewItem}>
        + Добавить участника
      </Button>
      <div className="cards">
        {commonPart !== 100 && (
          <Alert
            message="Общая доля участников не равна 100%. Проверьте доли участников компании."
            type="warning"
            // closable
            // onClose={onClose}
          />
        )}
        {members.map((member) => {

          const name = member.form === MemberFormEnum.legal ? 'ООО ' + member.company_name : `${member.sirname} ${member.firstname} ${member.lastname}`

          return (
            <Card
              hoverable
              // title={`${member.sirname} ${member.firstname} ${member.lastname}`}
              title={name}
              key={member.id}
              onClick={() => handleChangeItem(member)}
            >
              <div>
                {member.type &&
                  member.type.map((item, index) => (
                    <Tag key={index} color="green">
                      {MEMBERS_TYPES[item]}
                    </Tag>
                  ))}
              </div>
              <div>Паспорт: {member.passport}</div>
              {member.part && <div>Доля в компании: {member.part}%</div>}
            </Card>
          );
        })}
      </div>
    </div>
  );
};
