import axios from "axios";
import { utimes } from "fs";
import { api } from "shared/config/api";

export const DADATA_API = {
  async getCompany(query: string) {
    try {
      const options = await axios.post(
        "https://suggestions.dadata.ru/suggestions/api/4_1/rs/suggest/party",
        { query },
        {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: "Token f72f5381ea6760b06b1034d298d2c9bce5824346",
          },
        }
      );
    //   const result = options.data.suggestions
      const result = options.data.suggestions.map((item: any) => ({inn: item.data.inn, name: item.value}))
      console.log("🚀 ~ file: dadata.ts ~ line 19 ~ getCompany ~ result", result)
      return result;
    } catch (error) {}
  },
};
