import { Button, Form, Input } from "antd";
import { ICompanyRequisites } from "entities/company/types";
import { FC, useState } from "react";



interface IPropc {
  requisites: ICompanyRequisites;
}

export const RequisitiesBlock: FC<IPropc> = ({ requisites }) => {
  const [isActiveForm, setIsActiveForm] = useState(true);
  const [form] = Form.useForm();

  const activeHandler = () => {
    setIsActiveForm(!isActiveForm);
  };

  if (!requisites.inn) {
    return null;
  }

  return (
    <div>
      <Form
        form={form}
        disabled={isActiveForm}
        layout="vertical"
        initialValues={requisites}
      >
        <Form.Item
          name="name"
          label="Название"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="inn"
          label="ИНН"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="kpp"
          label="КПП"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="ogrn"
          label="ОГРН"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="address"
          label="Юридический адрес"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
      </Form>
      {isActiveForm ? (
        <Button type="primary" onClick={activeHandler}>
          Изменить
        </Button>
      ) : (
        <Button type="primary" onClick={activeHandler}>
          Сохранить
        </Button>
      )}
    </div>
  );
};
