import { useModal } from "@ebay/nice-modal-react";
import { Button, Card } from "antd";
import MeetingForm from "entities/meeting/form";
import { IMeeting, MEETING_TYPES, MEETING_TYPES_OF_CAUSE } from "entities/meeting/types";
import { useStore } from "entities/store";
import moment from "moment";
import { FC, useCallback } from "react";
import { useNavigate } from "react-router-dom";

interface IProps {
  meetings: any[];
  company_id: string;
}

export const MeetingsBlock: FC<IProps> = ({ meetings, company_id }) => {
  const userModal = useModal(MeetingForm);
  const { addMeeting } = useStore((state) => state);

  const history = useNavigate();

  const handleNewItem = useCallback(() => {
    userModal.show().then((newItem: any) => {
      addMeeting(company_id, newItem.name!);
    });
  }, [userModal, meetings]);

  const clickHandler = (id: string) => {
    // setCurrentMeeting(id);
    history("/meetings/" + id);
  };

  return (
    <div>
      <Button type="primary" onClick={handleNewItem}>
        + Добавить собрание
      </Button>
      <div className="cards">
        {meetings.map((meeting) => {
          return (
            <Card
              hoverable
              key={meeting.id}
              title={meeting.name}
              onClick={() => clickHandler(meeting.id)}
            >  
              <div>{meeting.date_of_meeting}</div>          
              <div>{MEETING_TYPES_OF_CAUSE[meeting.type_of_cause]}</div>
              <div>{MEETING_TYPES[meeting.type]}</div>
            </Card>
          );
        })}
      </div>
    </div>
  );
};
