import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, InputNumber, Modal, Select } from "antd";
import { DADATA_API } from "features/dadata";
import { useCallback, useState } from "react";
import { CompanySearch } from "shared/companysearch";
import {
  IMember,
  MEMBER_TYPES,
  MemberTypeEnum,
  MEMBERS_FORMS,
  MemberFormEnum,
} from "./types";

const { Option } = Select;

export default NiceModal.create(({ member }: { member: IMember }) => {
  const [isOwner, setIsOwner] = useState<boolean>(
    member ? member.type.includes(MemberTypeEnum.owner) : true
  );
  const [memberForm, setMemberForm] = useState<MemberFormEnum>(
    member && member.form
  );
  const [options, setOptions] = useState<any[]>([]);

  const modal = useModal();
  const [form] = Form.useForm();

  const handleSubmit = useCallback(() => {
    form.validateFields().then(() => {
      const newMember = { ...form.getFieldsValue() };
      modal.resolve(newMember);
      modal.hide();
    });
  }, [
    modal,
    // user,
    form,
  ]);

  const onSearch = async (value: string) => {
    const newOptions = await DADATA_API.getCompany(value);
    setOptions(newOptions);
  };

  const handleChangeMemberType = (values: any[]) => {
    if (values.includes(MemberTypeEnum.owner)) {
      setIsOwner(true);
    } else {
      setIsOwner(false);
    }
  };

  const handleChangeFormType = (value: MemberFormEnum) => {
    setMemberForm(value);
  };

  const manForm = (
    <>
      <Form.Item
        name="firstname"
        label="Имя"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="lastname"
        label="Отчество"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="sirname"
        label="Фамилия"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="passport"
        label="Паспортные данные"
        rules={[
          {
            // required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>

      {/* <Form.Item
        name="type"
        label="Статус лица"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Select
          mode="multiple"
          allowClear
          style={{ width: "100%" }}
          onChange={handleChangeMemberType}
        >
          {MEMBER_TYPES.map((member_type) => (
            <Option key={member_type.en}>{member_type.ru}</Option>
          ))}
        </Select>
      </Form.Item> */}

      <Form.Item
        name="part"
        label="Доля в компании, %"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <InputNumber min={0} max={100} />
      </Form.Item>
    </>
  );

  const legalForm = (
    <>
      <Form.Item
        name="inn"
        label="ИНН"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        {/* <Input /> */}
        {/* <CompanySearch /> */}
        <Select
          showSearch
          filterOption={false}
          onSearch={onSearch}
          // notFoundContent={fetching ? <Spin size="small" /> : null}
        >
          {options.map((option) => (
            <Option value={option.inn}>
              <span>{option.name} </span>
              <span style={{ color: "gray", fontSize: "10px" }}>
                {option.inn}
              </span>
            </Option>
          ))}
        </Select>
      </Form.Item>
      <Form.Item
        name="base"
        // label="Основание представления общества"
        label="Должность представителя в родительском падеже"
        initialValue='Генерального директора'
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="firstname"
        label="Имя представителя в родительском падеже"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="lastname"
        label="Отчество представителя в родительском падеже"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="sirname"
        label="Фамилия представителя в родительском падеже"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="passport"
        label="Паспортные данные представителя"
        rules={[
          {
            // required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Input />
      </Form.Item>

      {/* <Form.Item
        name="type"
        label="Роль участника"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <Select
          mode="multiple"
          allowClear
          style={{ width: "100%" }}
          onChange={handleChangeMemberType}
        >
          {MEMBER_TYPES.map((member_type) => (
            <Option key={member_type.en}>{member_type.ru}</Option>
          ))}
        </Select>
      </Form.Item> */}

      <Form.Item
        name="part"
        label="Доля в компании, %"
        rules={[
          {
            required: true,
            message: "Необходимо заполнить данное поле",
          },
        ]}
      >
        <InputNumber min={0} max={100} />
      </Form.Item>
    </>
  );

  return (
    <Modal
      {...antdModal(modal)}
      title="Лицо"
      //   okText={user ? "Update" : "Create"}
      onOk={handleSubmit}
      okText="Сохранить"
      cancelText="Отмена"
    >
      <Form form={form} initialValues={member} layout="vertical">
        <Form.Item
          name="form"
          label="Физическое или юридическое лицо"
          rules={[
            {
              required: true,
              message: "Необходимо заполнить данное поле",
            },
          ]}
        >
          <Select
            allowClear
            style={{ width: "100%" }}
            onChange={handleChangeFormType}
          >
            {Object.keys(MEMBERS_FORMS).map((item: any, index: any) => (
              <Option key={index} value={item}>
                {MEMBERS_FORMS[item]}
              </Option>
            ))}
          </Select>
        </Form.Item>

        {!memberForm
          ? null
          : memberForm === MemberFormEnum.legal
          ? legalForm
          : manForm}
      </Form>
    </Modal>
  );
});
